.font {
    width: calc(100% + 18px);
    margin-left: -9px;
    background-color: #00897B;
    font-family: Light;
    z-index: 0;
}

.listeInscription {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    left: 0;
}

.InscriptionC1 {
    width: 30%;
    text-align: center;
    color: white;
}

.InscriptionC1Container {
    padding-top: 60%;
    padding-bottom: 28%;
}

.InscriptionC1Picture {
    width: 175px;
    height: 175px;
    text-align: center;
}

.InscriptionC2 {
    width: 70%;
}

.InscriptionC2Image {
    width: 100%;        
}

.InscriptionListeChamps {
    list-style-type: none;
    padding-left: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
}

.InscriptionListeChampsChamp {
    width: 50%;
}

@media screen and (max-width: 950px){
    .font {
        width: 103%;
        height: 30%;
        background-color: #00897B;
        font-family: Light;
    }

    .listeInscription {
        display: flex;
        flex-direction: column;
        list-style-type: none;
        list-style-position: inside;
        padding: 0;
    }
    .InscriptionC1 {
        width: 100%;
        text-align: center;
        color: white;
    }

    .InscriptionC1Container {
        padding-top: 20%;
        padding-bottom: 10%;
    }

    .InscriptionC1Picture {
        display: none;
    }

    .InscriptionC2 {
        width: 100%;
        position: static;
        left: 0px;
    }

    .InscriptionC2Image {
        display: none;
    }

    .InscriptionListeChamps {
        list-style-type: none;
        padding-left: 0;
        width: 70%;
        margin-left: 15%;
        display: flex;
        flex-direction: column;
        margin-right: 15%;
    }

    .InscriptionListeChampsChamp {
        width: 100%
    }
}