.MdpBoxModifier {
    width: 100%;
    height: 90%;
    border: none;
    border-radius: 15px;
    font-size: 18px;
    padding-left: 20px;
    box-sizing: border-box;
}

.MdpBoxModifier:focus {
    width: 100%;
    height: 90%;
    border: none;
    border-radius: 15px;
    font-size: 18px;
    padding-left: 20px;
    box-sizing: border-box;
    outline: none;
}

.color {
    color :rgb(161, 161, 161)
}