.SeparationPageEvents{
    width: 100%;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    padding: 0;
    left: 0;
}

.NavBarPageEvents {
    width: 20%;
}

.ContainerPageEvents {
    width: 80%;
    padding-left: 20px;
}

.btnNavBarEventsBold {
    font-size: 20px;
    margin-top: 20px;
    font-weight: bold;
    background-color: white;
    border: none;
    cursor: pointer;
}

.btnNavBarEvents {
    font-size: 20px;
    margin-top: 20px;
    background-color: white;
    border: none;
    cursor: pointer;
}

.EventTitleAndThemes{
    color: white;
    text-align: center;
    position: absolute;
    left: 30%;
    top: 110px;
    width: 40%;

}

.EventThemes{
    display: flex;
    flex-direction: row;
    list-style-type: none;
    padding: 0;
    width: 40%;
    margin-left: auto;
    margin-right: auto;
    color: black;
}

.ChoixCatPageEvent{
    display: flex;
    flex-direction: column;
    list-style-type: none;
}

.ButtonEvents {
    float: right;
    margin-top: 30px;
    margin-right: 30px;
    margin-bottom: 30px;
}

@media screen and (max-width: 925px){

    .ChoixCatPageEvent{
        display: flex;
        flex-direction: row;
        list-style-type: none;
        padding: 0;
        width: 90%;
        margin-left: 5%;
    }

    .EventTitleAndThemes{
        color: white;
        text-align: center;
        position: absolute;
        left: 0%;
        top: 110px;
        width: 100%;
    
    }

    .EventThemes{
        display: none;
    }

    .ButtonEvents {
        float: initial;
        margin-top: 30px;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .SeparationPageEvents{
        width: 100%;
        display: flex;
        flex-direction: column;
        list-style-type: none;
        padding: 0;
        left: 0;
        overflow-x: hidden;
    }

    .NavBarPageEvents {
        width: 100%;
    }
    
    .ContainerPageEvents {
        width: 100%;
        padding-left: 20px;
    }
}