.navPerso {
    width: 82px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: linear-gradient(to bottom right, #00897B, #06e089);
    z-index: 1;
    border-right: 2px solid rgb(138, 129, 129);
    transition: width 250ms;
}

.navPerso2 {
    width: 300px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: linear-gradient(to bottom right, #00897B, #06e089);
    z-index: 1;
    border-right: 2px solid rgb(138, 129, 129);
}

.navPersoListe {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding: 0;
}

.PictureLiStyleHeaderDash {
    border-bottom: 2px solid #17c2b1;
    height: 118px;
    position: relative;
    top: -25px;
}

.ItemLiStyleHeaderDash {
    padding-bottom: 10px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    color: white;
    font-family: Light;
    font-size: 20px;
}

.ImageNavBarDashboard {
    margin-top: 25px;
    margin-left: 25px;
    width: 30px;
    height: 30px;
}

@media screen and (max-width: 800px){
    .navPerso {
        width: 100%;
        height: 80px;
        position: fixed;
        top: 0;
        left: 0;
        background: linear-gradient(to bottom right, #00897B, #06e089);
        z-index: 1;
        border-bottom: 2px solid rgb(138, 129, 129);
    }
    
    .navPerso2 {
        width: 300px;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background: linear-gradient(to bottom right, #00897B, #06e089);
        z-index: 1;
        border-bottom: 2px solid rgb(138, 129, 129);
    }
    
    .navPersoListe {
        list-style-type: none;
        padding: 0px;
        display: flex;
        flex-direction: row;
        margin-left: 30px;
    }
    
    .PictureLiStyleHeaderDash {
        border-bottom: none;
        height: 90px;
        position: relative;
        top: -25px;
    }
    
    .ItemLiStyleHeaderDash {
        margin-right: 0px;
        padding-right: 20px;
        margin-left: 0px;
        width: 30px;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        color: white;
        font-family: Light;
        font-size: 20px;
    }

    .ImageNavBarDashboard {
        margin-left: 0px;
        margin-top: 25px;
        margin-bottom: 25px;
        width: 25px;
        height: 25px;
        position: relative;
        top: -15px;
    }
}