.nav {
    height: 100px;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    background-color: white;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0;
    border-bottom: 2px solid rgb(138, 129, 129);
    z-index: 1;
}

.logo {
    height: 75px; 
    margin-right: 50px;
    margin-left: 20px;
}

.liste{
    list-style-type: none;
    height: 100px;
    display: flex;
    align-items: center;
    position: relative;
    margin: 0;
    padding: 0;
}

.logoNotShown {
    height: 75px; 
    margin-top: 10px;
    margin-right: calc(50% - 37.5px);
    margin-left: calc(50% - 37.5px);
    justify-content: center;
}

.listeNotShown {
    list-style-type: none;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    margin: 0;
    padding: 0;
}

.items{
    margin-right: 10px;
    cursor: pointer;
    padding-left: 10px;
    padding-right: 10px;
}

.itemsConnexion {
    display: none;
}

.itemsConnect{
    justify-content: right;
    cursor: pointer;
}

.rightOfScreenHeaderListe {
    position: absolute;
    top: 0px;
    right: 10px;
    list-style-type: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0;
    
}

.btn{
    display: none;
    height: 45px;
    width: 50px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}

@media screen and (max-width: 800px){
    .liste{
        flex-direction: column;
        height: 300px;
        background-color: white;
        border-bottom: 2px solid rgb(138, 129, 129);
    }

    .rightOfScreenHeaderListe {
        display: none;
    }

    .logo {
        height: 75px; 
        margin-top: 10px;
        margin-right: 0px;
        margin-left: 0px;
    }
    .items:nth-child(1){
        border-top: 2px solid black;
        background-color: white;
        margin-top: 50px;
    }
    
    .items {
        height: 50px;
        width: 100%;
        background-color: white;
        text-align: center;
        line-height: 50px;
        justify-content: center;
    }

    .itemsConnexion {
        display: block;
        height: 50px;
        width: 100%;
        background-color: white;
        text-align: center;
        line-height: 50px;
        justify-content: center;
    }
    
    .btn{
        display: block;
        position: absolute;
        top: 10px;
        right: 10px;
    }
}