.font {
    width: 100%;
    height: 40%;
    background-color: #00897B;
    font-family: Light;
    z-index: 0;
}

.listeConnexion {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    overflow-y: hidden;
}

.ConnexionC1 {
    width: 30%;
    position: static;
    text-align: center;
    background-color: #00897B;
    color: white;
}

.ConnexionC1Container {
    padding-top: 50%;
}

.ConnexionC1Picture {
    width: 175px;
    height: 175px;
    text-align: center;
}

.ConnexionC2 {
    width: 70%;
}

.ConnexionC2Image {
    width: 100%;
    margin-top: -50px;      
}

.ConnexionListeChamps {
    list-style-type: none;
    padding-left: 0;
    width: 40%;
    margin-left: 30%;
    margin-right: 30%;
}

@media screen and (max-width: 950px){
    .font {
        width: 100%;
        background-color: #00897B;
        font-family: Light;
    }

    .listeConnexion {
        display: flex;
        flex-direction: column;
        list-style-type: none;
        list-style-position: inside;
        padding: 0;
        width: 100%;
        left: 0;
    }

    
    .ConnexionC1 {
        width: 100%;
        text-align: center;
        color: white;
    }

    .ConnexionC1Container {
        padding-top: 20%;
        padding-bottom: 10%;
    }

    .ConnexionC1Picture {
        display: none;
    }

    .ConnexionC2 {
        width: 100%;
        position: static;
        left: 0px;
    }

    .ConnexionC2Image {
        display: none;
    }

    .ConnexionListeChamps {
        list-style-type: none;
        padding-left: 0;
        width: 70%;
        margin-left: 15%;
        margin-right: 15%;
    }
}