.GreenUserListContainer{
    width: 90%;
    margin-left: 5%;
    background-color: #0ba37b;
    border-radius: 25px;

}

.WhiteUserListContainer{
    margin-left: 30px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-right: 30px;
    width: calc(100% - 60px);
    height: 600px;
}

.ListeUserList{
    background-color: white;
    border-radius: 3px;
    list-style-type: none;
    padding: 0;
    overflow-y: scroll;
    max-height: 580px;
}

.borderBelowUserListBold{
    border-bottom: 2px solid #0b9973;
    height: 50px;
}
.borderBelowUserList{
    border-bottom: 1px solid #0b9973;
    height: 50px;
}

.CollonePseudoAdmin {
    width: 14%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

@media screen and (max-width: 1400px){
    .GreenUserListContainer{
        width: 94%;
        margin-left: 3%;
        background-color: #0ba37b;
        border-radius: 25px;
    
    }

    .CollonePseudoAdmin {
        width: 50%;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }
}