.Font404{
    background: linear-gradient(45deg, #1d2029, #222c47);
    background-size: 400% 400%;
    position: relative;
    overflow: hidden;
    width: calc(100% + 33px);
    margin-left: -25px;
    height: calc(100vh + 50px);
    animation: changeColor 40s infinite;
}

@keyframes changeColor {
    0% {
      background-position: 0% 50%; /* Position initiale du dégradé */
    }
    50% {
      background-position: 100% 50%; /* Position du dégradé à mi-chemin de l'animation */
    }
    100% {
      background-position: 0% 50%; /* Retour à la position initiale du dégradé */
    }
  }