.containerEventBox {
    margin-top: 50px;
    height: 350px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    z-index: 0;
}

.listeEventBox {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
}

.texteInfos{
    text-align: right;
    font-size: 20px;
}

.EventPicture{
    max-width: 85%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
}

.collone1 {
    width: 40%;
    height: 350px;
    list-style-type: none;
}

.collone2 {
    width: 40%;
    height: 350px;
    list-style-type: none;
}

.collone3 {
    width: 20%;
    height: 350px;
    margin-top: 40px;
    list-style-type: none;
}

@media screen and (max-width: 1250px){
    .containerEventBox {
        margin-top: 0px;
        width: 100%;
        height: auto;
        margin-right: auto;
        margin-left: auto;
        z-index: 0;
        margin-bottom: 0px;
    }

    .listeEventBox {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        padding: 0;
        border-bottom: 4px solid black;
    }

    .texteInfos{
        text-align: center;
        font-size: 15px;
        margin-top: 0px;
    }
    
    .collone1 {
        width: 100%;
        list-style-type: none;
    }

    .EventPicture{
        width: 100%;
        min-width: 100%;
    }

    .collone2 {
        width: 90%;
        height: auto;
        margin-bottom: 30px;
        text-align: center;
        list-style-type: none;
        margin-top: -60px;
    }

    .collone3 {
        width: 90%;
        margin-top: 0px;
        height: auto;
        text-align: center;
        list-style-type: none;
        margin-bottom: 20px;
    }
}