.DashboardListeChoix{
    display: flex;
    flex-direction: row;
    list-style-type: none;
    padding: 0;
    width: 100%;
    position: relative;
    margin-left: 8.33%;
    top: -120px;
}


.largeurDashboardItem{
    width: 25%;
    margin-top: 0px;
    margin-bottom: 0px;
}

@media screen and (max-width: 1400px){
    .DashboardListeChoix{
        display: flex;
        flex-direction: column;
        list-style-type: none;
        padding: 0;
        width: 100%;
        position: unset;
        top: 0;
    }

    .largeurDashboardItem{
        width: 100%;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}