.ListeDeBouttonAdminUser {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    margin-left: 5%;
}

.ButtonContainerAdminUser{
    margin-left: 20px;
}

.BannerAdminUser {
    width: 100%;
    margin-top: -300px;
}

@media screen and (max-width: 1400px){
    .ListeDeBouttonAdminUser {
        list-style-type: none;
        padding: 0;
        display: flex;
        flex-direction: column;
        margin-left: 0%;
    }

    .ButtonContainerAdminUser{
        margin-left: calc(50% - 100px);
        margin-top: 20px;
    }

    .BannerAdminUser {
        width: 120%;
        margin-top: -20px;
        margin-left: -20px;
    }
}