.DashboardBoxContainer {
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    height: 480px;
    background: #0b9973;
    border-radius: 35px;
    border: 3px solid #0b9973;
}

.DashboardBoxContainer:hover {
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    height: 480px;
    background: #0da47c;
    border-radius: 35px;
    border: 3px solid #0da47c;
}

.DashboardBoxListe{
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding: 0;
}

.DashboardBoxImageContaniner{
    margin-left: calc(10% + 20px);
    margin-right: calc(10% + 20px);
    padding-bottom: 50px;
    width: calc(80% - 40px);
    border-bottom: 1px solid #0bad82;
    display: flex;
    justify-content: center;
}

.DashboardBoxImage {
    max-width: 80%;
    max-height: 330px;
    margin-top: 50px;
}