.ContainerProjectBox {
    margin-top: 100px;
    margin-bottom: 100px;
}

.ListeProjectBox {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: row;
}

.Collone1ProjectBox {
    width: 30%;
}

.ImageProjectBox {
    height: 100%;
    max-width: 100%;
}

.Collone2ProjectBox {
    width: 70%;
    padding-left: 20px;
}

.VideoYoutubeProject {
    margin-left: calc(50% - 280px);
    margin-top: 20px;
    margin-bottom: 20px;
    width: 560px;
    height: 315px;
}

@media screen and (max-width: 650px){
    .VideoYoutubeProject {
        margin-left: calc(50% - 175px);
        margin-top: 20px;
        margin-bottom: 20px;
        width: 350px;
        height: 210px;
    }
}

@media screen and (max-width: 950px) {
    .ListeProjectBox {
        list-style-type: none;
        padding: 0;
        display: flex;
        flex-direction: column;
    }

    .Collone1ProjectBox {
        width: 100%;
    }

    .Collone2ProjectBox {
        width: 100%;
        padding-left: 20px;
    }
}